a{
  cursor: pointer;
}
.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: rgba(80, 80, 80, 0);
  color: white;
  background-image: url(./imgs/toronto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-header {
  background-image: url(./imgs/toronto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#date{
  width: 100px;
  text-align: left;
  padding-right: 30px;
}
.dates-container{
  width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
#event{
  height: 100%;
  margin: 0;
  padding: 5px 0 5px 0;
  top: 0;
  font-weight: 500;
  font-size: 20px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: space-evenly;
}
#event:hover{
  text-decoration: underline;
  cursor: pointer;
}
.event-container{
  padding-top: 10px;
}
#herman-picture{
  background-image: url(./imgs/trees.jpg);
  width: 300px;
  height: 300px;
  background-position: center;
  background-size: 90%;
  border-radius: 100%;
}
.herman-info-box{
  width: 680px;
  margin-left: 20px;
}
img{
  content: url(./imgs/vector.png);
}
.intro{
  width: 1000px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-evenly;
  padding: 20px;
  transition: 0.7s;
  background-color: rgba(80, 80, 80, 0.5);
}
.intro:hover{
  transition: 0.7s;
  background-color: rgba(80, 80, 80, 0.95);
  color: white;  
}
.intro-box{
  height: auto;
  width: 90%;
  color: rgb(150, 150, 150);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
}
.intro-box:hover{
  background-image: none;
}
#month{
  font-size: 35px;
  font-weight: 700;
}
.month-container{
  width: 900px;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-top: 40px;
}
#photo-captions{
  margin-top: 20vh;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.61);
}
.projects{
  width: 1000px;
}
.projects-box{
  min-height: 40vh;
  width: 100%;
}
#subtitle{
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 1vmin);
  font-weight: 700;
}
.spacer{
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 55vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.spacer-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.time-container{
  width: 1000px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: rgb(80, 80, 80, 0.5);
  transition: 0.7s;
}
.time-container:hover{
  transition: 0.7s;
  background-color: rgb(80, 80, 80, 0.95);
}

.timeline{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#time-slot{
  width: 100%;
  height: 100%;
  color: rgb(150, 150, 150);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#time-slot:hover{
  color: rgb(255, 255, 255);
}
#title{
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: calc(45px + 1vmin);
  font-weight: 700;
  padding-bottom: 15px;
}

.title-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  position: relative;
}

ul {
  list-style-type: square;
  list-style-position: outside;
  list-style-image: none;
}

@media (max-width: 500px) {
  #date{
    font-size: calc(10px + 2vmin);
  }
  #event-info{
    font-size: calc(10px + 2vmin);
  }
  #month{
    font-size: calc(25px + 1vmin);
    font-weight: 700;
  }
  .month-container{
    width: 100%;
    justify-content: center;
  }
  #subtitle{
  font-size: calc(10px + 1vmin);
  justify-content: flex-start;
  }
  #title{
    font-size: calc(25px + 1vmin);
    text-align: center;
  }
  .title-card{
    height: auto;
  }
}
@media (min-width: 501px) and (max-width: 999px){
  .month-container{
    margin-left: 10vw;
  }
}
@media (max-width: 999px) {
  #date{
    width: 10%;
    text-align: left;
    padding-right: 30px;
  }
  .dates-container{
    width: 80%;
  }
  #herman-picture{
    width: 250px;
    height: 250px;
  }
  .herman-info-box{
    width: 90%;
  }
  .intro{
    width: 90%;
    padding: 0.2vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(80, 80, 80, 0.95);
    color: white;  
  }
  .intro-box{
    height: auto;
    width: 100%;
    color: rgb(150, 150, 150);
    transition: 0.7s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .time-container{
    width: 100%;
  }
  .month-container{
    width: 100%;
  }
}

